import Vue from 'vue'
import Router from 'vue-router'
import CartList from '@/components/CartList'
import MainManual from '@/components/MainManual'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'manual',
            component: MainManual
        },
        {
            path: '/cart/:id',
            name: 'Cart',
            component: CartList
        }
    ]
})



