<template>
    <div id="manual">
        <div class="title">{{title}}</div>
        <div class="cart_list">
            <div v-for="(l, index) in list" :key="index" class="cart">
                <div v-show="!l.info_editable">
                    <router-link :to="{path: '/cart/' + l.id }">{{l.title}}</router-link>
                    <div class="cart_dead_time">
                        <div>
                            截止: {{l.dead_time}}
                        </div>
                        <div>
                            Sponsor: {{l.sponsor}}
                        </div>
                    </div>
                </div>
                <div v-show="l.info_editable">
                    <div>團　名: <input class="cart_title" v-model="l.title"></div>
                    <div>截　止: <input v-model="l.dead_time"></div>
                    <div>發起人: <input v-model="l.sponsor"></div>
                </div>
                <div class="cart_func">
                    <button @click="edit_cart(index)" v-show="!l.info_editable">修改</button>
                    <button @click="update_cart(index)" v-show="l.info_editable">{{l.info_editable_btn_text}}</button>
                </div>
            </div>
        </div>
        <div class="cart_func">
            <button @click="add_cart">開新團</button>
        </div>
    </div>
</template>

<script>
import { db } from '../db'

export default {
    data () {
        return {
            title: '所有團購',
            list: []
        }
    },
    methods: {
        add_cart: function () {
            let tmp = {
                id: '',
                title: '',
                dead_time: '',
                sponsor: '',
                info_editable: true,
                info_editable_btn_text: '確定'
            }
            this.list.push(tmp)
        },
        edit_cart: function (index) {
            this.list[index].info_editable = !this.list[index].info_editable
            this.list[index].info_editable_btn_text = (this.list[index].info_editable == false) ? '修改' : '確定'
        },
        update_cart: function (index) {
            if (this.list[index].title == '') {
                this.list.splice(index)
            } else {
                if (this.list[index].id == '') {
                    db.collection('cart/')
                        .add({
                            title: this.list[index].title,
                            dead_time: this.list[index].dead_time,
                            sponsor: this.list[index].sponsor,
                            timestamp: Date.now()
                        })
                        .then(doc => {
                            this.list[index].id = doc.id
                            this.list[index].info_editable = !this.list[index].info_editable
                            this.list[index].info_editable_btn_text = (this.list[index].info_editable == false) ? '修改' : '確定'
                        })
                }
                else {
                    db.doc('cart/' + this.list[index].id)
                        .update({
                            title: this.list[index].title,
                            dead_time: this.list[index].dead_time,
                            sponsor: this.list[index].sponsor
                        })
                        .then(() => {
                            this.list[index].info_editable = !this.list[index].info_editable
                            this.list[index].info_editable_btn_text = (this.list[index].info_editable == false) ? '修改' : '確定'
                        })
                }
            }
        }
    },
    mounted: function () {
        let cart_list = db.collection('cart/')
        console.log(cart_list)
        cart_list.get().then(data => {
            data.forEach(d => {
                let cart = d.data()
                let tmp = {
                    id: d.id,
                    title: cart.title,
                    dead_time: cart.dead_time,
                    sponsor: cart.sponsor,
                    info_editable: false,
                    info_editable_btn_text: '確定'
                }
                this.list.push(tmp)
            })
        })
    }
}
</script>

<style scoped>
.title {
    font-size: 30px;
    font-weight: bold;
    /* width: 350px; */
    /* border-top: 1px solid gray; */
    /* border-bottom: 1px solid gray; */
    padding: 20px 20px;
}

.cart_list {
    width: 350px;
    border-top: 1px solid gray;
    margin: 20px auto;
}

.cart {
    border-bottom: 1px solid gray;
    padding: 20px 0;
    font-size: 20px;
}

.cart_dead_time {
    font-size: 14px;
    color: blue;
    text-align: right;
    margin: 10px 10px;
}
</style>


