<template>
    <div id="cartlist">
        <div class="cart_title" v-show="!info_editable">{{title}}</div>
        <div class="cart_title" v-show="info_editable"><input class="cart_title" v-model="title"></div>
        <div  class="cart_info" v-show="!info_editable">
            <div class="cart_dead_time">截止: {{dead_time}}</div>
            <div class="cart_sponsor">Sponsor: {{sponsor}}</div>
        </div>
        <div  class="cart_info" v-show="info_editable">
            <div class="cart_dead_time">截止: <input v-model="dead_time"></div>
            <div class="cart_sponsor">Sponsor: <input v-model="sponsor"></div>
        </div>
        <div>
            <button @click="edit_info">{{info_editable_btn_text}}</button>
            <button @click="delete_cart">{{delete_btn_text}}</button>
        </div>
        <div class="cart_participants">
            <div v-for="(p, index) in participants" :key="index" class="cart_participant">
                <div v-show="p.participant_editable">
                    <div>
                        參團人: <input v-model="p.buyer">
                    </div>
                    <div>
                        團購品: <input v-model="p.product">
                    </div>
                    <div>
                        數　量: <input v-model="p.quantity">
                    </div>
                </div>
                <div v-show="!p.participant_editable">
                    {{p.buyer}}:  {{p.product}} x {{p.quantity}}
                </div>
                <div class="cart_participant_func">
                    <button @click="edit_participant(index)" v-show="!p.participant_editable">修改</button>
                    <button @click="update_participant(index)" v-show="p.participant_editable">{{p.participant_editable_btn_text}}</button>
                    <button @click="delete_participant(index)">刪除</button>
                </div>
            </div>
        </div>
        <button @click="add_participant">新增團員</button>
    </div>
</template>

<script>
import { db } from '../db'

export default {
    data () {
        return {
            currid: '',
            id: '',
            title: '載入中...',
            dead_time: '載入中...',
            sponsor: '載入中...',
            info_editable: false,
            info_editable_btn_text: '編緝資訊',
            delete_btn_text: '刪除團購',
            participants: []
        }
    },
    methods: {
        edit_info: function () {
            this.info_editable = !this.info_editable
            this.info_editable_btn_text = (this.info_editable == false) ? '編緝資訊' : '確定'
        },
        delete_cart: function() {
            if (this.delete_btn_text == '刪除團購') {
                this.delete_btn_text = '要刪嗎??'
            }
            else {
                this.title = "刪除中..."
                this.dead_time = '刪除中...'
                this.sponsor = '刪除中...'
                this.delete_btn_text = '刪除團購'
                db.doc('cart/' + this.currid)
                    .delete()
                    .then(() => {
                        window.location = '/'
                    })
            }
        },
        add_participant: function() {
            this.participants.push({
                id: '',
                buyer: '',
                product: '',
                quantity: 1,
                participant_editable: true,
                participant_editable_btn_text: '新增'
            })
        },
        edit_participant: function (index) {
            this.participants[index].participant_editable = !this.participants[index].participant_editable
            this.participants[index].participant_editable_btn_text = (this.participants[index].participant_editable == false) ? '修改' : '確定'
        },
        update_participant: function (index) {            
            if (this.participants[index].buyer == '') {
                this.participants.splice(index)
            } else {
                if (this.participants[index].id == '') {
                    db.collection('cart/' + this.currid + '/participants')
                        .add({
                            buyer: this.participants[index].buyer,
                            product: this.participants[index].product,
                            quantity: this.participants[index].quantity,
                            timestamp: Date.now()
                        })
                        .then(doc => {
                            this.participants[index].id = doc.id
                            this.participants[index].participant_editable = !this.participants[index].participant_editable
                            this.participants[index].participant_editable_btn_text = (this.participants[index].participant_editable == false) ? '修改' : '確定'
                        })
                }
                else {
                    db.doc('cart/' + this.currid + '/participants/' + this.participants[index].id)
                        .update({
                            buyer: this.participants[index].buyer,
                            product: this.participants[index].product,
                            quantity: this.participants[index].quantity
                        })
                        .then(() => {
                            this.participants[index].participant_editable = !this.participants[index].participant_editable
                            this.participants[index].participant_editable_btn_text = (this.participants[index].participant_editable == false) ? '修改' : '確定'
                        })
                }
            }
        },
        delete_participant: function (index) {
            db.doc('cart/' + this.currid + '/participants/' + this.participants[index].id)
                .delete()
                .then(() => {
                    this.participants.splice(index, 1)
                })
        }
    },
    mounted: function () {
        this.currid = this.$route.params.id
        let cart = db.doc('cart/' + this.currid)
        cart.get().then(data => {
            let d = data.data()
            this.id = data.id
            this.title = d.title
            this.dead_time = d.dead_time
            this.sponsor = d.sponsor
            
            let parts = db.collection('cart/' + this.currid + '/participants').orderBy('timestamp')
            parts.get().then(data => {
                data.forEach(dd => {
                    let p = dd.data()
                    let tmp = {
                        id: dd.id,
                        buyer: p.buyer,
                        product: p.product,
                        quantity: p.quantity,
                        participant_editable: false,
                        participant_editable_btn_text: '修改',
                        delete_btn_text: '刪除團購',
                    }
                    this.participants.push(tmp)
                })
            })
        })
    }
}
</script>

<style scoped>
.cart_title {
    font-size: 30px;
    font-weight: bold;
}

.cart_info {
    width: 350px;
    /* border-top: 1px solid gray; */
    /* border-bottom: 1px solid gray; */
    margin: 20px auto;
}

.cart_dead_time {
    color: blue;
    text-align: right;
    margin: 10px 10px;
}

.cart_sponsor {
    text-align: right;
    margin: 10px 10px;
}

.cart_participants {
    width: 350px;
    border-top: 1px solid gray;
    margin: 20px auto;
}

.cart_participant {
    border-bottom: 1px solid gray;
    padding: 20px 20px;
}

.cart_participant_func {
    margin: 10px 10px;
}
</style>


